:root {
  --form-input-border-color: rgb(180, 180, 180);
}

.container {
  width: 100%;
  padding: 20px 20px 40px 20px;
}

.form {
  display: grid;
  grid-template-columns: repeat(2, minmax(200px, 1fr));
  column-gap: 10px;
  row-gap: 20px;
  min-width: 200px;
  max-width: 100%;
}

.input {
  height: 30px;
  font-size: 1.15rem;
  border: solid;
  border-color: var(--form-input-border-color);
  border-radius: 5px;
}

@media screen and (max-width: 800px) {
  .form {
    grid-template-columns: 1fr;
  }
}
